import { Component, OnInit } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { MarketService } from '../core/market.service';
import { UtilityService } from '../core/utility.service';

@Component({
  selector: 'app-marketplace',
  templateUrl: './marketplace.page.html',
  styleUrls: ['./marketplace.page.scss'],
})
export class MarketplacePage implements OnInit {
  profiletab: string = 'all';
  products;
  dogProducts;
  catProducts;
  filterDogs;
  filterCats;
  filterProducts;
  fitlerValue;
  types;
  loaded = false;
  label;
  filter = [
    {
      name: 'Kibble/Dry',
      value: 'kibbleDry',
    },
    {
      name: 'Canned',
      value: 'canned',
    },
    {
      name: 'Semi-Moist',
      value: 'semiMoist',
    },
    {
      name: 'homeCooked',
      value: 'Home Cooked',
    },
  ];
  constructor(
    private marketService: MarketService,
    private utility: UtilityService,
    private activateRoute: ActivatedRoute
  ) {}

  ngOnInit() {
    this.configurePage();
    this.getProducts();
    this.getPetType();
  }

  configurePage() {
    let category = this.activateRoute.snapshot.queryParams.category;
    if (category == 'all') {
      this.label = 'all';
    } else if (category == 'food') {
      this.label = 'food';
    } else if (category == 'medicines') {
      this.label = 'medicines';
      this.filter = [];
    } else if (category == 'accessories') {
      this.label = 'accessories';
      this.filter = [];
    } else if (category == 'pets') {
      this.label = 'pets';
      this.filter = [];
    }
  }

  getProducts() {
    this.utility.present();
    let catId = this.activateRoute.snapshot.queryParams.id;
    this.marketService.getProducts(catId, 1, 10).subscribe(
      (res) => {
        console.log(res);
        this.products = res;
        this.filterProducts = this.products;
        this.getCatProducts();
        this.getDogProducts();
        this.utility.dismiss();
      },
      (err) => {
        this.utility.dismiss();
      }
    );
  }
  getCatProducts() {
    this.catProducts = this.products.filter((v) => v.petTypeId.name === 'cat');
    this.filterCats = this.catProducts;
  }
  getDogProducts() {
    this.dogProducts = this.products.filter((v) => v.petTypeId.name === 'dog');
    this.filterDogs = this.dogProducts;
  }
  handleFilter(e, tab) {
    const val = e.target.value;
    console.log(val, tab);
    if (tab === 'cat') {
      if (val == 'all') {
        return this.getCatProducts();
      }
      this.catProducts = this.products.filter(
        (v) => v.productType === val && v.petTypeId.name === 'cat'
      );
    } else {
      if (val == 'all') {
        return this.getDogProducts();
      }
      this.dogProducts = this.products.filter(
        (v) => v.productType === val && v.petTypeId.name === 'dog'
      );
    }
    console.log(this.catProducts, this.dogProducts);
  }
  handleSearch(e, type) {
    if (type == 'dog') {
      this.filterDogs = this.dogProducts.filter((name) => {
        name = name.name.toLowerCase();
        return name.indexOf(e.target.value) > -1;
      });
    } else if (type == 'cat') {
      this.filterCats = this.catProducts.filter((name) => {
        name = name.name.toLowerCase();
        return name.indexOf(e.target.value) > -1;
      });
    } else if (type == 'all') {
      this.filterProducts = this.products.filter((name) => {
        name = name.name.toLowerCase();
        return name.indexOf(e.target.value) > -1;
      });
    }
    console.log(this.filterDogs);
  }
  segmentChanged(e) {
    console.log(e);
    this.getDogProducts();
    this.getCatProducts();
    const { detail } = e;
    if (detail == 'all') {
    } else {
    }
  }
  clearFilter() {
    this.fitlerValue = '';
    this.getDogProducts();
    this.getCatProducts();
  }
  goBack() {
    this.utility.goBack();
  }
  getPetType() {
    this.marketService.getPetType().subscribe((res) => {
      console.log(res, 'types');
      this.types = res;
    });
  }
}
